import { Component, OnInit } from '@angular/core';
import { OrphanService } from './shared/orphan.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'orphan-web';
  navBar: String;

  constructor(private orphanService:OrphanService){}

  ngOnInit() {
    this.orphanService.navBarStatus.subscribe(navBar => this.navBar = navBar);

      
  }

}
