import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import {Router} from "@angular/router";
import { ReactiveFormsModule, FormGroup, FormBuilder, Validators, FormsModule } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ActivatedRoute } from '@angular/router';

import { OrphanService } from '../../shared/orphan.service'
import { Orphan } from '../../shared/orphan'
import { User } from 'src/app/shared/user';

@Component({
  selector: 'app-new-saudi-orphan',
  templateUrl: './new-saudi-orphan.component.html',
  styleUrls: ['./new-saudi-orphan.component.css']
})
export class NewSaudiOrphanComponent implements OnInit {

  @ViewChild("name") nameField:ElementRef;
  saudiTotalRecords: any;
  newForm: FormGroup;

  headerTitle: string;
  gender: any;
  org_id: any;
  user: User={
    name:'',
    email:'',
    org_id:''
  };

  constructor(
    private tostr: ToastrService,
    private orphanService: OrphanService,
    private router: Router,
    public fb: FormBuilder,
    private route: ActivatedRoute
  ){this.route.params.subscribe(params => {this.gender = params.gender; this.ngOnInit()})}

  ngOnInit() {
    this.orphanService.changeNavBarStatus("block");
    this.getHeaderTitle();
    this.getUser();
    this.orphanForm();
  }

  getHeaderTitle(){
    if(this.gender == 'male') this.headerTitle = "إضافة سعودي (ذكر)"
    if(this.gender == 'female') this.headerTitle = "إضافة سعودي (أنثى)"
  }

  getUser(){
    this.orphanService.getUser().subscribe(
      user =>{
        this.user = user;
        this.org_id = this.user.org_id;
        this.getTotalRecords();
      }
    );
  }

  getTotalRecords(){
    this.orphanService.getSaudiTotalRecords(this.org_id, this.gender).subscribe(
      res =>{
        this.saudiTotalRecords = res;
      }
    );
  }

  private orphanForm() {
    this.newForm = this.fb.group({
      firstName: ['', [Validators.required]],
      secondName: ['', [Validators.required]],
      thirdName: [''],
      title: ['', [Validators.required]],
      status: [''],
      idNo: ['']
      // address: ['', [Validators.required]],
    });
  }

  // Using getters will make your code look pretty
  get firstName() { return this.newForm.get('firstName') }
  get secondName() { return this.newForm.get('secondName') }
  get thirdName() { return this.newForm.get('thirdName') }
  get title() { return this.newForm.get('title') }
  get status() { return this.newForm.get('status') }
  get nationality() { return this.newForm.get('nationality') }
  get idNo() { return this.newForm.get('idNo') }
  // get address() { return this.newForm.get('address') }

  public newOrphan() {

    if (!this.newForm.valid) return null;
    const vals = this.newForm.value;
    
    const orphan: Orphan = {
      gender: this.gender,
      firstName: this.firstName.value,
      secondName: this.secondName.value,
      thirdName: this.thirdName.value,
      title: this.title.value,
      status: this.status.value,
      nationality: 'Saudi',
      idNo: this.idNo.value,
      // address: this.address.value,
      makfoul: 0,
      createdBy: this.user.id,
      org: this.org_id
    }

    this.orphanService.newOrphan(orphan)
    .subscribe(
      res => {
        console.log(orphan.createdBy);
        this.newForm.reset();
        this.tostr.success('تم التسجيل بنجاح');
        this.getTotalRecords();
        this.nameField.nativeElement.focus();
      },
      err => this.tostr.error('خطأ ' + err)
      // err => console.error(err)
    )
    
  }

}
