import { Component, OnInit, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import {MatDialog, MatDialogConfig} from '@angular/material';

import { OrphanService } from '../../shared/orphan.service';
import { Orphan } from 'src/app/shared/orphan';
import { EditOrphanComponent } from '../edit-orphan/edit-orphan.component';
import { AddKafeelToOrphanComponent } from 'src/app/kafeels/add-kafeel-to-orphan/add-kafeel-to-orphan.component';
import { User } from 'src/app/shared/user';

@Component({
  selector: 'app-orphans-table',
  templateUrl: './orphans-table.component.html',
  styleUrls: ['./orphans-table.component.css']
})
export class OrphansTableComponent implements OnInit {

  user: User={
    name:'',
    email:'',
    org_id:''
  };

  firstName: string;

  gender: any;
  org_id: any;
  headerTitle: string;
  orphans: Orphan[];
  serial: number = 1;

  constructor(
    private orphanService: OrphanService,
    private route: ActivatedRoute, private dialog: MatDialog
  ) { this.route.params.subscribe(params => {this.gender = params.gender; this.ngOnInit()}) }
  

  ngOnInit() {
    this.getUser();
  }

  getUser(){
    this.orphanService.getUser().subscribe(
      user =>{
        this.user = user;
        this.org_id = user.org_id;
        this.getOrhans();
      }
    );
  }

  getHeaderTitle(){
    if(this.gender == 'male') this.headerTitle = "بيان السعوديين (ذكور)"
    if(this.gender == 'female') this.headerTitle = "بيان السعوديين (إناث)"
    if(this.gender == 'nonSaudi') this.headerTitle = "بيان غير السعوديين"
  }

  getOrhans(){
    return this.orphanService.getOrphans(this.org_id, this.gender).subscribe(
      data => {
        this.orphans = data;
        this.getHeaderTitle();
      }, 
    );
  }

  onEdit(id){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = {
      orphan_id : id,
    }
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '55%';
    const dialogCallBack = this.dialog.open(EditOrphanComponent, dialogConfig);
    dialogCallBack.afterClosed().subscribe(() => {
      this.getOrhans();
    });
  }

  onAddKafeelToOrphan(id){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = {
      orphan_id : id,
    }
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = false;
    dialogConfig.width = '70%';
    const dialogCallBack = this.dialog.open(AddKafeelToOrphanComponent, dialogConfig);
    dialogCallBack.afterClosed().subscribe(() => {
      this.getOrhans();
    });
  }

  search(){
    if(this.firstName !=""){
      this.orphans = this.orphans.filter(res => {
        return res.firstName.match(this.firstName);
      });
    }else{
      this.ngOnInit();
    }
  }



}
