import { Component, OnInit, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';

import { OrphanService } from '../../shared/orphan.service';
import { Orphan } from 'src/app/shared/orphan';

@Component({
  selector: 'app-makfouled-orphans',
  templateUrl: './makfouled-orphans.component.html',
  styleUrls: ['./makfouled-orphans.component.css']
})
export class MakfouledOrphansComponent implements OnInit {

  org_id: any;
  headerTitle: string;
  orphans: Orphan[];
  serial: number = 1;

  constructor(
    private orphanService: OrphanService,
    private route: ActivatedRoute
  ) { this.route.params.subscribe(params => {this.org_id = params.org_id}) }
  

  ngOnInit() {
    this.getOrhans();
  }


  getOrhans(){
    return this.orphanService.getMakfouledOrphans(this.org_id).subscribe(
      data => {
        this.orphans = data;
      }
    );
  }


}
