import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

// toastr notification
import { ToastrModule } from 'ngx-toastr';

import { MaterialModule } from './shared/material'

import { OrphanService } from './shared/orphan.service';
import { FooterComponent } from './footer/footer.component';
import { NewNonSaudiOrphanComponent } from './orphans/new-non-saudi-orphan/new-non-saudi-orphan.component';
import { OrphansTableComponent } from './orphans/orphans-table/orphans-table.component';
import { NavbarComponent } from './core/navbar/navbar.component';
import { LoginComponent } from './core/login/login.component';
import { SignupComponent } from './core/signup/signup.component';
import { ProfileComponent } from './core/profile/profile.component';
import { RequestResetComponent } from './core/password/request-reset/request-reset.component';
import { ResponseResetComponent } from './core/password/response-reset/response-reset.component';

// jwt services
import { JarwisService } from './services/jarwis.service';
import { TokenService } from './services/token.service';
import { AuthService } from './services/auth.service';
import { AfterLoginService } from './services/after-login.service';
import { BeforeLoginService } from './services/before-login.service';
// import { SnotifyModule, SnotifyService, ToastDefaults } from 'ng-snotify';

import { TokenInterceptorService } from './shared/token-interceptor.service';
import { EditOrphanComponent } from './orphans/edit-orphan/edit-orphan.component';

// import sites module
import { SitesModule } from './sites/sites.module';

// new org component
import { NewOrgComponent } from './orgs/new-org/new-org.component';
import { NewKafeelComponent } from './kafeels/new-kafeel/new-kafeel.component';
import { KefeelsListComponent } from './kafeels/kefeels-list/kefeels-list.component';
import { AddKafeelToOrphanComponent } from './kafeels/add-kafeel-to-orphan/add-kafeel-to-orphan.component';
import { OrphansPublicTableComponent } from './orphans/orphans-public-table/orphans-public-table.component';
import { NewSaudiOrphanComponent } from './orphans/new-saudi-orphan/new-saudi-orphan.component';
import { MakfouledOrphansComponent } from './orphans/makfouled-orphans/makfouled-orphans.component';
import { KafalaApproveComponent } from './orphans/kafala-approve/kafala-approve.component';
import { CardsListComponent } from './cards/cards-list/cards-list.component';
import { FillCardComponent } from './cards/fill-card/fill-card.component';
import { PreviewCardComponent } from './cards/preview-card/preview-card.component';

@NgModule({
  declarations: [
    AppComponent,
    FooterComponent,
    NewNonSaudiOrphanComponent,
    OrphansTableComponent,
    NavbarComponent,
    LoginComponent,
    SignupComponent,
    ProfileComponent,
    RequestResetComponent,
    ResponseResetComponent,
    EditOrphanComponent,
    NewOrgComponent,
    NewKafeelComponent,
    KefeelsListComponent,
    AddKafeelToOrphanComponent,
    OrphansPublicTableComponent,
    NewSaudiOrphanComponent,
    MakfouledOrphansComponent,
    KafalaApproveComponent,
    CardsListComponent,
    FillCardComponent,
    PreviewCardComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    ToastrModule.forRoot({
      timeOut: 2000,
      positionClass: "toast-bottom-full-width"
    }),
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    MaterialModule,
    SitesModule,
  ],
  providers: [OrphanService, JarwisService, TokenService, AuthService, AfterLoginService, BeforeLoginService,
    // { provide: 'SnotifyToastConfig', useValue: ToastDefaults },
    // SnotifyService
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptorService,
      multi: true
    }
  ],
  bootstrap: [AppComponent],
  entryComponents:[EditOrphanComponent,AddKafeelToOrphanComponent,NewKafeelComponent, PreviewCardComponent]
})
export class AppModule { }
