import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import { HttpClient } from '@angular/common/http';
import { ReactiveFormsModule, FormGroup, FormBuilder, Validators, FormsModule } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

import { ToastrService } from 'ngx-toastr';
import { OrphanService } from '../../shared/orphan.service';
import { Kafeel } from 'src/app/shared/kafeel';
import { User } from 'src/app/shared/user';

@Component({
  selector: 'app-new-kafeel',
  templateUrl: './new-kafeel.component.html',
  styleUrls: ['./new-kafeel.component.css']
})
export class NewKafeelComponent implements OnInit {

  newForm: FormGroup;

  org_id: any;
  user: User={
    name:'',
    email:'',
    org_id:''
  };

  constructor(
    private tostr: ToastrService,
    private router: Router,
    private orphanService: OrphanService,
    public fb: FormBuilder,
    private http: HttpClient,
    public dialogRef: MatDialogRef<NewKafeelComponent>,
    ) {
  }

  ngOnInit() {
    this.kafeelForm();
    this.getUser();
  }

  getUser(){
    this.orphanService.getUser().subscribe(
      user =>{
        this.user = user;
        this.org_id = this.user.org_id;
      }
    );
  }

  private kafeelForm() {
    this.newForm = this.fb.group({
      name: ['', [Validators.required]],
      city: ['', [Validators.required]],
      mobile: ['', [Validators.required]]
    });
  }

  // Using getters will make your code look pretty
  get name() { return this.newForm.get('name') }
  get mobile() { return this.newForm.get('mobile') }
  get city() { return this.newForm.get('city') }

  public newKafeel() {

    if (!this.newForm.valid) return null;
    const vals = this.newForm.value;
    
    const kafeel: Kafeel = {
      name: this.name.value,
      mobile: this.mobile.value,
      city: this.city.value,
      org: this.org_id
    }

    this.orphanService.newKafeel(kafeel)
    .subscribe(
      res => {
        console.log(res);
        this.dialogRef.close();
        this.tostr.success('تم اضافة كفيل جديد بنجاح');
      },
      err => console.error(err)
    )


  }

  onClose(){
    // this.newBreadwinnerForm.reset();
    this.dialogRef.close();
  }

}