import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders } from '@angular/common/http';
import { Observable, observable, BehaviorSubject } from 'rxjs';

import { TokenService } from '../services/token.service'
import { Configuration } from './configuration';

import { Org } from './org';
import { Orphan } from './orphan';
import { User } from './user';
import { Kafeel } from './kafeel';

@Injectable({
  providedIn: 'root'
})
export class OrphanService {

  private config = new Configuration();

  private navBar = new BehaviorSubject<String>("none");
  navBarStatus = this.navBar.asObservable();

  user: User;

  constructor(private http: HttpClient, private tokenService: TokenService) { }

  changeNavBarStatus(navBar: String){
    this.navBar.next(navBar);
  }

  // add new org
  newOrg(org: Org): Observable<Org>{
      return this.http.post<Org>(this.config.apiUrl + 'new-org', org)
  }

  // get orphans
  getOrphans(org_id:string, gender:string):Observable<Orphan[]>{
    let params = new HttpParams().set('org_id', org_id).set('gender', gender);
    return this.http.get<Orphan[]>(this.config.apiUrl + 'orphans' , { params })
  }

  // get only makfouled orphans
  getMakfouledOrphans(org_id:string):Observable<Orphan[]>{
    let params = new HttpParams().set('org_id', org_id);
    return this.http.get<Orphan[]>(this.config.apiUrl + 'makfouled-orphans' , { params })
  }

  // add new orphan
  newOrphan(orphan: Orphan): Observable<Orphan>{
      return this.http.post<Orphan>(this.config.apiUrl + 'new-orphan', orphan)
  }

  // get total of saudi male orphans
  getSaudiTotalRecords(org_id:string, gender:string){
    let params = new HttpParams().set('org_id', org_id).set('gender', gender);
    return this.http.get(this.config.apiUrl + 'saudi-total-records' , { params })
  }

  // get total of saudi male orphans
  nonSaudiTotalRecords(){
    return this.http.get(this.config.apiUrl + 'non-saudi-total-records')
  }
  
  // get single orphan
  getOrphan(id:number): Observable<Orphan>{
    return this.http.get<Orphan>(this.config.apiUrl + 'orphan/' + id);
  }

  // update orphan
  updateOrphan(orphan:Orphan): Observable<Orphan>{
    return this.http.post<Orphan>(this.config.apiUrl + 'update-orphan' , orphan);
  }

  // add kafeel to orphan
  addKafeelToOrphan(orphan:Orphan): Observable<Orphan>{
    return this.http.post<Orphan>(this.config.apiUrl + 'add-kafeel-to-orphan' , orphan);
  }

  // get logged in user details
  getUser(){
    // let headers = new HttpHeaders().set('Authorization', 'bearer ' + token)
    return this.http.post<User>(this.config.apiUrl + 'me', '')
  }

  // get kafeels for an org
  // getKafeels():Observable<Kafeel[]>{
  //   return this.http.get<Kafeel[]>(this.config.apiUrl + 'kafeels-list')
  // }


  // get kafeels for an org
  getKafeels(org_id:string):Observable<Kafeel[]>{
    let params = new HttpParams().set('org_id', org_id);
    return this.http.get<Kafeel[]>(this.config.apiUrl + 'kafeels-list' , { params })
  }

  // add new kafeel
  newKafeel(kafeel: Kafeel): Observable<Kafeel>{
      return this.http.post<Kafeel>(this.config.apiUrl + 'new-kafeel', kafeel)
  }



}
