import { Component, OnInit } from '@angular/core';
import { EmbedVideoService } from 'ngx-embed-video';


@Component({
  selector: 'app-khamis-harb-waqf',
  templateUrl: './khamis-harb-waqf.component.html',
  styleUrls: ['./khamis-harb-waqf.component.css']
})
export class KhamisHarbWaqfComponent implements OnInit {


  yt_iframe_html: any;
  
  youtubeUrl = "https://www.youtube.com/watch?v=tnivQHZ0NUE";

  constructor(
    private embedService: EmbedVideoService
  ) {
  }

  ngOnInit() {
    this.getYtUrl(this.youtubeUrl);
  }

  getYtUrl(ytUrl){
    this.yt_iframe_html = this.embedService.embed(ytUrl, {
      query: { portrait: 0, color: '333' },
      attr: { style: 'position:absolute;top:0;left:0;width:100%;height:100%' }
    });
  }


}
