import { Component, OnInit } from '@angular/core';
import { ReactiveFormsModule, FormGroup, FormBuilder, Validators, FormsModule, FormArray } from '@angular/forms';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import { OrphanService } from '../../shared/orphan.service';
import { Orphan } from 'src/app/shared/orphan';
import { Kafeel } from 'src/app/shared/kafeel';
import { User } from 'src/app/shared/user';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-kafala-approve',
  templateUrl: './kafala-approve.component.html',
  styleUrls: ['./kafala-approve.component.css']
})
export class KafalaApproveComponent implements OnInit {

  form: FormGroup;

  
  orphans: string[] = ['واحد', 'اثنين', 'ثلاثة'];
  filteredOrphans: Observable<string[]>;

  kafeels: any;
  filteredKafeels: any;
  
  constructor(
    private orphanService: OrphanService,
    private route: ActivatedRoute,
    public fb: FormBuilder
  ) {
    this.form = fb.group({
      kafeel : ["", Validators.required],
      orphan : ["", Validators.required],
      kafalaType : ["", Validators.required],
      amount : ["", Validators.required],
    });
  }

  // kafeel
  private _filterKafeel(kafeel: string): string[] {
    const filterKafeelValue = kafeel.toLowerCase();

    return this.kafeels.filter(kafeel => kafeel.toLowerCase().indexOf(filterKafeelValue) === 0);
  }

  // orphan
  private _filterOrphan(orphan: string): string[] {
    const filterOrphanValue = orphan.toLowerCase();

    return this.orphans.filter(orphan => orphan.toLowerCase().indexOf(filterOrphanValue) === 0);
  }


  submit(){
    console.log(this.form.controls['kafeel'].value);
  }




  
  ngOnInit() {

    this.orphanService.getKafeels("1").subscribe(
      kafeels => {
        for(let key in kafeels){

          this.kafeels = kafeels[key].name
          console.log()
        }
      }
    );



    // kafeel
    this.filteredKafeels = this.form.controls['kafeel'].valueChanges.pipe(
      startWith(''),
      map(kafeel => this._filterKafeel(kafeel)),
    );

    // orphan
    this.filteredOrphans = this.form.controls['orphan'].valueChanges.pipe(
      startWith(''),
      map(orphan => this._filterOrphan(orphan)),
    );

  }


}
