import { Component, OnInit, ViewChild } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';

import { OrphanService } from '../../shared/orphan.service';
import { Orphan } from 'src/app/shared/orphan';

@Component({
  selector: 'app-orphans-public-table',
  templateUrl: './orphans-public-table.component.html',
  styleUrls: ['./orphans-public-table.component.css']
})
export class OrphansPublicTableComponent implements OnInit {

  org_id: any;
  gender: any;
  headerTitle: string;
  orphans: Orphan[];
  serial: number = 1;

  constructor(
    private orphanService: OrphanService,
    private route: ActivatedRoute
  ) { this.route.params.subscribe(params => {this.org_id = params.org_id, this.gender = params.gender}) }
  

  ngOnInit() {
    this.getHeaderTitle();
    this.getOrhans();
  }

  getHeaderTitle(){
    if(this.gender == 'male') this.headerTitle = "بيان تفصيلي بالمكفولين (ذكور)"
    if(this.gender == 'female') this.headerTitle = "بيان تفصيلي بالمكفلولين (إناث)"
  }

  getOrhans(){
    return this.orphanService.getOrphans(this.org_id, this.gender).subscribe(
      data => {
        this.orphans = data;
      }
    );
  }


}
