import { Component, OnInit, AfterViewInit, ElementRef } from '@angular/core';
import { EmbedVideoService } from 'ngx-embed-video';
import {MatDialog, MatDialogConfig} from '@angular/material';
import { ModalComponent } from '../../modal/modal.component';

@Component({
  selector: 'app-kh-waqf',
  templateUrl: './kh-waqf.component.html',
  styleUrls: ['./kh-waqf.component.css']
})
export class KhWaqfComponent implements OnInit, AfterViewInit  {

  images = new Array(8)

  yt_iframe_html: any;
  
  youtubeUrl = "https://www.youtube.com/watch?v=Vki9uK1o1WA";

  constructor(
    private embedService: EmbedVideoService, private dialog: MatDialog, private elementRef: ElementRef
  ) {
  }

  
  ngAfterViewInit(){
    this.elementRef.nativeElement.ownerDocument.body.style.backgroundColor = '#DDEEF2';
    this.elementRef.nativeElement.ownerDocument.body.style.backgroundImage = "url('assets/sites/khamis-harb-waqf/new-pattern.png')";
  }



  ngOnInit() {
    this.getYtUrl(this.youtubeUrl);
  }

  getYtUrl(ytUrl){
    this.yt_iframe_html = this.embedService.embed(ytUrl, {
      query: { portrait: 0, color: '333' },
      attr: { style: 'position:absolute;top:0;left:0;width:100%;height:100%' }
    });
  }

  onCreateNew(imgName: string){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.data = imgName;
    dialogConfig.autoFocus = true;
    const dialogCallBack = this.dialog.open(ModalComponent, dialogConfig);
  } 


}
