import { Component, OnInit, ViewChild } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
import {MatDialog, MatDialogConfig} from '@angular/material';

import { DataSource } from '@angular/cdk/collections';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { OrphanService } from '../../shared/orphan.service';
import { Kafeel } from 'src/app/shared/kafeel';
import { NewKafeelComponent } from '../new-kafeel/new-kafeel.component';
import { User } from 'src/app/shared/user';


@Component({
  selector: 'app-kefeels-list',
  templateUrl: './kefeels-list.component.html',
  styleUrls: ['./kefeels-list.component.css']
})
export class KefeelsListComponent implements OnInit {

  dataSource = new MatTableDataSource();

  kafeels: Kafeel[];
  listData: any;
  serial: number = 1;

  org_id: any;
  user: User={
    name:'',
    email:'',
    org_id:''
  };


  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;

  displayedColumns = ['index', 'name', 'mobile', 'city'];

  constructor(
    private orphanService: OrphanService,
    private route: ActivatedRoute,
    private dialog: MatDialog,
    private http: HttpClient
  ) { }

  ngOnInit() {
    this.orphanService.changeNavBarStatus("block");
    this.getUser();
  }

  getUser(){
    this.orphanService.getUser().subscribe(
      user =>{
        this.user = user;
        this.org_id = this.user.org_id;
        this.getKafeels();
      }
    );
  }
  
  getKafeels(){
    return this.orphanService.getKafeels(this.org_id).subscribe(
      res => {
        if(res){
          this.dataSource.data = res
          this.listData = this.dataSource
          this.kafeels = res
        }
      }
    );
  }

  ngAfterViewInit(){
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }
  
  applyFilter(filterValue: string){
    filterValue = filterValue.trim();
    filterValue = filterValue.toLocaleLowerCase();
    this.dataSource.filter = filterValue;
  }

  onCreateNew(){
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '55%';
    const dialogCallBack = this.dialog.open(NewKafeelComponent, dialogConfig);
    dialogCallBack.afterClosed().subscribe(() => {
      this.getKafeels();
    });
  } 

}
