import { Component, OnInit, Input, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';

import { OrphanService } from '../../shared/orphan.service'
import { Kafeel } from '../../shared/kafeel'
import { Orphan } from 'src/app/shared/orphan';
import { User } from 'src/app/shared/user';

@Component({
  selector: 'app-add-kafeel-to-orphan',
  templateUrl: './add-kafeel-to-orphan.component.html',
  styleUrls: ['./add-kafeel-to-orphan.component.css']
})
export class AddKafeelToOrphanComponent implements OnInit {

  today= new Date();
   dd = this.today.getDate();
   mm = this.today.getMonth() + 1; //January is 0!
   yyyy = this.today.getFullYear();
  
  @Input() orphan_id : number;

  user: User={
    name:'',
    email:'',
    org_id:''
  };

  org_id: any;

  makfoolName: string;

  kafeels: Kafeel[];

  orphan: Orphan={
    kafeel:"",
    kafalaType:"",
    amount:""
    // kafalaDate: new Date()
  };


  constructor(
    private orphanService: OrphanService,
    private tostr: ToastrService,
    public dialogRef: MatDialogRef<AddKafeelToOrphanComponent>,
    @Inject(MAT_DIALOG_DATA) data
  ) { 
    this.orphan_id = data.orphan_id
  }

  ngOnInit() {

    this.orphanService.getOrphan(this.orphan_id).subscribe(
      data => {
        this.makfoolName = data.firstName + " " + data.secondName + " " + data.thirdName + " " + data.title;
      }
    )
    
    this.orphanService.getUser().subscribe(
      user =>{
        this.user = user;
        this.org_id = user.org_id;
        this.getKafeels();
      }
    ); 

  }

  getKafeels(){
    this.orphanService.getKafeels(this.org_id).subscribe(
      res => {
             this.kafeels = res;
       }
     );
  }

  onSubmit(){
    this.orphanService.getOrphan(this.orphan_id).subscribe(
      data => {
        this.orphan.id = data.id
        this.orphan.makfoul = 1

        this.orphanService.addKafeelToOrphan(this.orphan)
        .subscribe(
          res => {
            this.tostr.success('تم تسجيل كفالة بنجاح');
            this.dialogRef.close();
          },
          err => this.tostr.error('خطأ ' + err)
          // err => console.error(err)
        )
            
      }
      
    )

    console.log(this.orphan);
  }

  onClose(){
    // this.newBreadwinnerForm.reset();
    this.dialogRef.close();
  }

}
