import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import { ReactiveFormsModule, FormGroup, FormBuilder, Validators, FormsModule } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { OrphanService } from '../../shared/orphan.service';

import { Org } from '../../shared/org'

@Component({
  selector: 'app-new-org',
  templateUrl: './new-org.component.html',
  styleUrls: ['./new-org.component.css']
})
export class NewOrgComponent implements OnInit {

  newForm: FormGroup;

  constructor(
    private tostr: ToastrService,
    private orphanService: OrphanService,
    private router: Router,
    public fb: FormBuilder,
    ) {
  }

  ngOnInit() {
    this.orphanService.changeNavBarStatus("block");
    this.orphanForm();
  }

  private orphanForm() {
    this.newForm = this.fb.group({
      orgName: ['', [Validators.required]],
    });
  }

  // Using getters will make your code look pretty
  get orgName() { return this.newForm.get('orgName') }

  public newOrg() {

    if (!this.newForm.valid) return null;
    const vals = this.newForm.value;
    
    const org: Org = {
      name: this.orgName.value,
    }

    this.orphanService.newOrg(org)
    .subscribe(
      res => {
        this.newForm.reset();
        this.tostr.success('تم التسجيل بنجاح');
      },
      err => this.tostr.error('خطأ ' + err)
      // err => console.error(err)
    )
    
  }

}
