import { Component, OnInit, ViewChild } from '@angular/core';
import {MatDialog, MatDialogConfig} from '@angular/material';
import { PreviewCardComponent } from '../../../cards/preview-card/preview-card.component';
import { ReactiveFormsModule, FormGroup, FormBuilder, Validators, FormsModule, FormArray } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

declare var $:any;

@Component({
  selector: 'app-new-card',
  templateUrl: './new-card.component.html',
  styleUrls: ['./new-card.component.css']
})
export class NewCardComponent implements OnInit {
  category: any;
  card: any;

  form: FormGroup;
  activeCard:number;
  to:string = "";
  from:string = "";

  constructor(
    private dialog: MatDialog,
    public fb: FormBuilder,
    private route: ActivatedRoute
  ){
    this.route.params.subscribe(params => {this.category = params.category;this.card = params.card;})
    this.activeCard = this.card;
    this.form = fb.group({
      from : ["", Validators.required],
      to : ["", Validators.required],
    });
  }

  ngOnInit() {
    $('#cdk-overlay-0').css("max-width", "100vw");
  }

  previewCard(activeCard:number){
    this.to = this.form.controls['to'].value;
    this.from = this.form.controls['from'].value;
    const dialogConfig = new MatDialogConfig();
    dialogConfig.data = {
      card : activeCard,
      to: this.to,
      from:this.from
    }
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = false;
    dialogConfig.height = '1100px';

    const dialogCallBack = this.dialog.open(PreviewCardComponent, dialogConfig);
    // dialogCallBack.afterClosed().subscribe(() => {
    //   this.getOrhans();
    // });
  }

}
