import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './core/login/login.component';
import { SignupComponent } from './core/signup/signup.component';
import { ProfileComponent } from './core/profile/profile.component';

import { BeforeLoginService } from './services/before-login.service';
import { AfterLoginService } from './services/after-login.service';


import { NewNonSaudiOrphanComponent } from './orphans/new-non-saudi-orphan/new-non-saudi-orphan.component';
import { OrphansTableComponent } from './orphans/orphans-table/orphans-table.component';

// sites
import { KhamisHarbEkfalniComponent } from './sites/khamis-harb/khamis-harb-ekfalni/khamis-harb-ekfalni.component';
import { MarwaEkfalniComponent } from './sites/marwa/marwa-ekfalni/marwa-ekfalni.component';
import { NewOrgComponent } from './orgs/new-org/new-org.component';
import { KefeelsListComponent } from './kafeels/kefeels-list/kefeels-list.component';
import { KhamisHarbEkfalniStepsComponent } from './sites/khamis-harb/khamis-harb-ekfalni-steps/khamis-harb-ekfalni-steps.component';
import { OrphansPublicTableComponent } from './orphans/orphans-public-table/orphans-public-table.component';
import { NewSaudiOrphanComponent } from './orphans/new-saudi-orphan/new-saudi-orphan.component';
import { MakfouledOrphansComponent } from './orphans/makfouled-orphans/makfouled-orphans.component';
import { KhamisHarbWaqfComponent } from './sites/khamis-harb/khamis-harb-waqf/khamis-harb-waqf.component';
import { KhWaqfComponent } from './sites/khamis-harb/kh-waqf/kh-waqf.component';
import { MrWaqfComponent } from './sites/marwa/mr-waqf/mr-waqf.component';
import { RaithWaqfComponent } from './sites/raith/raith-waqf/raith-waqf.component';
import { RaithProjectsComponent } from './sites/raith-projects/raith-projects.component';
import { DmStepsComponent } from './sites/dm-steps/dm-steps.component';
import { RaithStepsComponent } from './sites/raith-steps/raith-steps.component';
import { AidabWaqfComponent } from './sites/aidab/aidab-waqf/aidab-waqf.component';
import { KafalaApproveComponent } from './orphans/kafala-approve/kafala-approve.component';
import { AidabEkfalniComponent } from './sites/aidab/aidab-ekfalni/aidab-ekfalni.component';
import { AidabStepsComponent } from './sites/aidab-steps/aidab-steps.component';
import { ArishWaqfComponent } from './sites/arish/arish-waqf/arish-waqf.component';
import { ArishProjectsComponent } from './sites/arish/arish-projects/arish-projects.component';
import { ArishStepsComponent } from './sites/arish/arish-steps/arish-steps.component';
import { NewGiftComponent } from './sites/gits/new-gift/new-gift.component';
import { ChooseCategoryComponent } from './sites/cards/choose-category/choose-category.component';
import { ChooseCardComponent } from './sites/cards/choose-card/choose-card.component';
import { SaveCardComponent } from './sites/cards/save-card/save-card.component';
import { CardsListComponent } from './cards/cards-list/cards-list.component';
import { FillCardComponent } from './cards/fill-card/fill-card.component';
import { NewCardComponent } from './sites/cards/new-card/new-card.component';

const routes: Routes = [

  { path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  },

  // sites
  // { path: 'khamis-harb-ekfalni', component:KhamisHarbEkfalniComponent },
  // { path: 'khamis-harb-ekfalni-steps', component:KhamisHarbEkfalniStepsComponent },
  // { path: 'khamis-harb-waqf', component:KhamisHarbWaqfComponent },
  // { path: 'kh-waqf', component:KhWaqfComponent },
  {path:'orphans-public/:org_id/:gender', component:OrphansPublicTableComponent},
  {path:'makfouled-orphans/:org_id', component:MakfouledOrphansComponent},
  { path: 'marwa-ekfalni', component:MarwaEkfalniComponent },
  { path: 'mr-waqf', component:MrWaqfComponent },
  { path: 'raith-waqf', component:RaithWaqfComponent },
  { path: 'raith-projects', component:RaithProjectsComponent },
  { path: 'dm-steps', component:DmStepsComponent },
  // { path: 'raith-steps', component:RaithStepsComponent },
  { path: 'aidab-waqf', component:AidabWaqfComponent },
  { path: 'aidab-ekfalni', component:AidabEkfalniComponent },
  { path: 'aidab-steps', component:AidabStepsComponent },
  { path: 'arish-waqf', component:ArishWaqfComponent },
  { path: 'arish-projects', component:ArishProjectsComponent },
  { path: 'arish-steps', component:ArishStepsComponent },
  { path: 'new-gift', component:NewGiftComponent },
  { path: 'choose-category', component:ChooseCategoryComponent },
  { path: 'choose-card/:category', component:ChooseCardComponent },
  { path: 'save-card/:category/:card', component:SaveCardComponent },
  { path: 'new-card/:category/:card', component:NewCardComponent },
  { path: 'cards-list', component:CardsListComponent },
  { path: 'fill-card/:card', component:FillCardComponent },
  { path: 'fill-card/:card', component:FillCardComponent },
  // { path: 'kaf-approve', component:KafalaApproveComponent }, // صفحة اعتماد الكفالة

  {
    path: 'login',
    component: LoginComponent,
    canActivate: [BeforeLoginService]
  },
  {
    path: 'signup',
    component: SignupComponent,
    canActivate: [BeforeLoginService]
  },

  {path: '', canActivate:[AfterLoginService], children: [
    {path:'orphans/:gender', component:OrphansTableComponent},
    {path:'new-saudi-orphan/:gender', component:NewSaudiOrphanComponent},
    {path:'new-non-saudi-orphan', component:NewNonSaudiOrphanComponent},
    { path: 'new-org', component:NewOrgComponent },
    { path: 'kafeels-list', component:KefeelsListComponent }
  ]},

  // {
  //   path: 'request-password-reset',
  //   component: RequestResetComponent,
  //   canActivate: [BeforeLoginService]
  // },
  // {
  //   path: 'response-password-reset',
  //   component: ResponseResetComponent,
  //   canActivate: [BeforeLoginService]
  // },


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
