import { Component, OnInit, Input, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { ReactiveFormsModule, FormGroup, FormBuilder, Validators, FormsModule } from '@angular/forms';
import { Orphan } from 'src/app/shared/orphan';
import { OrphanService } from 'src/app/shared/orphan.service';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-edit-orphan',
  templateUrl: './edit-orphan.component.html',
  styleUrls: ['./edit-orphan.component.css']
})
export class EditOrphanComponent implements OnInit {

  editForm: FormGroup;
  @Input() orphan_id : number;

  orphan: Orphan;

  constructor(
    private tostr: ToastrService,
    public dialogRef: MatDialogRef<EditOrphanComponent>,
    private orphanService: OrphanService,
    public fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA) data
    ) {
      this.orphan_id = data.orphan_id
  }

  ngOnInit() {
    this.orphanForm();
    this.orphanService.getOrphan(this.orphan_id).subscribe(
      data => {
        this.orphan = data;
      }
    )
  }

  private orphanForm() {
    this.editForm = this.fb.group({
      firstName: ['', [Validators.required]],
      secondName: ['', [Validators.required]],
      thirdName: [''],
      title: ['', [Validators.required]],
      status: [''],
      idNo: ['']
      // address: ['', [Validators.required]],
    });
  }

  // Using getters will make your code look pretty
  get firstName() { return this.editForm.get('firstName') }
  get secondName() { return this.editForm.get('secondName') }
  get thirdName() { return this.editForm.get('thirdName') }
  get title() { return this.editForm.get('title') }
  get status() { return this.editForm.get('status') }
  get idNo() { return this.editForm.get('idNo') }
  // get address() { return this.editForm.get('address') }

  public editOrphan() {

    if (!this.editForm.valid) return null;
    const vals = this.editForm.value;
    
    const orphan: Orphan = {
      id: this.orphan_id,
      firstName: this.firstName.value,
      secondName: this.secondName.value,
      thirdName: this.thirdName.value,
      title: this.title.value,
      status: this.status.value,
      idNo: this.idNo.value,
      // address: this.address.value,
    }

    this.orphanService.updateOrphan(orphan)
    .subscribe(
      res => {
        this.tostr.success('تم التحديث بنجاح');
        this.dialogRef.close();
      },
      err => this.tostr.error('خطأ ' + err)
      // err => console.error(err)
    )
    
  }

  onClose(){
    // this.newBreadwinnerForm.reset();
    this.dialogRef.close();
  }

}
