import { Component, OnInit } from '@angular/core';
import html2canvas from 'html2canvas';
import {Router} from "@angular/router";
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-save-card',
  templateUrl: './save-card.component.html',
  styleUrls: ['./save-card.component.css']
})
export class SaveCardComponent implements OnInit {

  category: any;
  card: any;

  to: string = "";
  from: string = "";

  constructor(
    private route: ActivatedRoute,
  ){this.route.params.subscribe(params => {this.category = params.category;this.card = params.card;})}

  ngOnInit() {
    document.getElementById("template").style.backgroundImage = "url('../../assets/sites/cards/" + this.category + "/" + this.card + ".jpg')";
    this.createCard("","");
    // this.createCard();
  }


  // update(value:string){
  //   var to = document.getElementById("to").value;
  //   var from = document.getElementById("from").value;
  //   var canvas = <HTMLCanvasElement> document.getElementById("canvas");
  //   var ctx = canvas.getContext('2d');
  //   ctx.clearRect(0, 0, canvas.width, canvas.height);
  //   ctx.fillText(to, 375, 375);
  //   ctx.fillText(from, 375, 623);
  // }

  updateTo(value:string){
    this.to = value;
  }

  updateFrom(value:string){
    this.from = value;
    console.log(this.from);
  }


  createCard(to:string, from:string){
    document.getElementById("template").style.backgroundImage = "url('../../assets/sites/cards/" + this.category + "/" + this.card + ".jpg')";
    html2canvas(document.getElementById("template")).then(function(canvas) {
      document.getElementById("myCard").appendChild(canvas);

      canvas = document.getElementById("myCard").children[0];
      var ctx = canvas.getContext('2d');
      ctx.font = '32px cairo';
      ctx.textAlign = "center";
      ctx.fillText(to, 375, 375);
      ctx.fillText(from, 375, 623);
      ctx.fillStyle= 'black';
      ctx.direction = 'rtl';
      
      canvas.setAttribute("id", "canvas");
 
      const link = document.getElementById("downloadLink");
      link.setAttribute("href", canvas.toDataURL());

      document.getElementById("downloadLink").setAttribute("download", "card.png");


    });

  }

  saveCard(){
    this.createCard(this.to,this.from);
  }
  

}